import { requestAxios } from '@/utils'
import qs from 'qs'

export default {
  /**
   * 登录
   * @param {*} params
   */
  /*
  login(params) {
    return requestAxios({
      url: `/auth/admin/login`,
      method: 'POST',
      data: qs.stringify(params)
    })
  },
  */
  /**
   * 获取--一级代理列表
   * @param {*} params
   */
  getOneAgentList(params) {
    return requestAxios({
      url: `/auth/company/opera/list_rewrite`,
      method: 'GET',
      params: params
    })
  },
  /**
   * 获取--二级代理列表
   * @param {*} params
   */
  getAgentList(params) {
    return requestAxios({
      url: `/auth/company/distribute/list_rewrite`,
      method: 'GET',
      params: params
    })
  },
  /**
   * 获取--三级代理列表
   * @param {*} params
   */
  getTreeAgentList(params) {
    return requestAxios({
      url: `/auth/company/third_agent/list_rewrite`,
      method: 'GET',
      params: params
    })
  },
  /**
   * 获取--代理--详情
   * @param {*} params
   */
  getAgentDetail(params) {
    return requestAxios({
      url: `/auth/company/distribute/detail`,
      method: 'GET',
      params: params
    })
  },
  /**
   * 获取--代理--提交审核
   * @param {*} params
   */
  submitAgentView(params) {
    return requestAxios({
      url: `/auth/company/distribute/audit`,
      method: 'post',
      params: params
    })
  },
  /**
   * 获取--代理--修改
   * @param {*} params
   */
  editAgentView(params) {
    return requestAxios({
      url: `/auth/company/distribute/update`,
      method: 'post',
      data: qs.stringify(
        params
      )
    })
  },
  /**
   * 新增代理-一级
   * @param {*} params
   */
  addAgentViewOne(params) {
    return requestAxios({
      url: '/auth/company/opera/save_rewrite',
      method: 'post',
      data: qs.stringify(
        params
      )
    })
  },
  /**
   * 新增代理-二级
   * @param {*} params
   */
  addAgentViewTwo(params) {
    return requestAxios({
      url: '/auth/company/distribute/save_rewrite',
      method: 'post',
      data: qs.stringify(
        params
      )
    })
  },
  /**
   * 新增代理-三级
   * @param {*} params
   */
  addAgentViewThree(params) {
    return requestAxios({
      url: '/auth/company/third_agent/save_rewrite',
      method: 'post',
      data: qs.stringify(
        params
      )
    })
  },
  /**
   * 获取--商户列表
   * @param {*} params
   */
  getClientList(params) {
    return requestAxios({
      url: `/merchant/merchant/find_by_company_id`,
      method: 'GET',
      params: params
    })
  },
  /**
   * 获取--商户列表(新)
   * @param {*} params
   */
  getClientListNew(params) {
    return requestAxios({
      url: `/merchant/merchant/re_list`,
      method: 'GET',
      params: params
    })
  },
  /**
   * 获取--商户详情
   * @param {*} params
   */
  getClienDetail(params) {
    return requestAxios({
      url: '/merchant/merchant/find_one',
      method: 'get',
      params: params
    })
  },
  /**
   * 获取--商户详情--编辑
   * @param {*} params
   */
  editClienDetail(params) {
    return requestAxios({
      url: '/merchant/merchant/update',
      method: 'post',
      data: qs.stringify(
        params
      )
    })
  },
  /**
   * 获取--商户详情--新增
   * @param {*} params
   */
  addClienDetail(params) {
    return requestAxios({
      url: '/merchant/merchant/save_rewrite',
      method: 'post',
      data: qs.stringify(
        params
      )
    })
  },
  /**
   * 获取--商户详情--重置密码
   * @param {*} params
   */
  resetPswClienDetail(params) {
    return requestAxios({
      url: '/merchant/merchant/default/reset_password',
      method: 'post',
      params: params
    })
  },
  /**
   * 获取--获取省市区
   * @param {*} params
   */
  getProvice(params) {
    return requestAxios({
      url: `/common/province/tree`,
      method: 'GET',
      params: params,
      jsonHeader: true
    })
  }
}



