import Vue from 'vue'
import Router from 'vue-router'
import * as types from './types'
import homeRoutes from './src/home'
import clientInfoRoutes from './src/clientInfo'
import agencyOrClientRoutes from './src/agencyOrClient'
import mineRoutes from './src/mine'
import resetPassword from './src/resetPassword'
Vue.use(Router)

const router = new Router({
  // mode: 'history',
  base: '/web/h5',
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: types.LOGIN,
      component: resolve => require(['@/pages/login/index.vue'], resolve),
      meta: {
        isShowFooter: false,
        title: '登录'
      }
    },
    ...homeRoutes,
    ...mineRoutes,
    ...clientInfoRoutes,
    ...agencyOrClientRoutes,
    ...resetPassword
  ]
})

router.beforeEach((to, from, next) => {
  if (to.path !== '/login') {
    // 判断是否存在token
    if (!sessionStorage.token && to.path !== '/fogot') {
      next({path: '/login'})
    } else if (to.path === '/fogot') {
      next()
    } else {
      next()
    }
  } else {
    sessionStorage.clear()
    next()
  }
})

router.afterEach(to => {
  window.scrollTo(0, 0)
})

export default router



