import { requestAxios } from '@/utils'
import qs from 'qs'

export default {
  /**
   * 登录
   * @param {*} params
   */
  login(params) {
    return requestAxios({
      url: `/auth/admin/login`,
      method: 'POST',
      data: qs.stringify(params)
    })
  },
  /**
   * 修改密码
   * @param {*} params
   */
  resetPsw(params) {
    return requestAxios({
      url: `/auth/user/modify_password`,
      method: 'POST',
      data: qs.stringify(params)
    })
  },
  /**
   * 获取我--信息
   * @param {*} params
   */
  getUserInfo(params) {
    return requestAxios({
      url: `/auth/running_account_new/my`,
      method: 'GET',
      params: params
    })
  },
   /**
   * 添加支付宝信息
   * @param {*} params
   */
  addAlipay(userId,name,aliAccountNumber) {
    return requestAxios({
      url: `/ali_pay_info/add`,
      method: 'POST',
      data: qs.stringify({
        userId,
        name,
        aliAccountNumber 
      })
    })
  },
     /**
   * 获取支付宝信息
   * @param {*} params
   */
  getAlipay(userId) {
    return requestAxios({
      url: `/ali_pay_info/get_ali_pay_info`,
      method: 'GET',
      params: {
        userId 
      }
    })
  },
}



