import { requestAxios } from '@/utils'
import qs from 'qs'

export default {
    /**
     * 我的商户
     * @param {*} params
     */
    myMerchant(companyId, payPriceType, commissionType) {
        return requestAxios({
            url: `/merchant/merchant/ger_merchant_list`,
            method: 'GET',
            params: {
                companyId,
                payPriceType,
                commissionType
            }
        })
    },
    /**
    * 代理发展商户
    * @param {*} params
    */
    agencyMerchant(companyId, payPriceType, merchantNumType, type) {
        return requestAxios({
            url: `/merchant/merchant/get_proxy_merchant_list`,
            method: 'GET',
            params: {
                companyId,
                payPriceType,
                merchantNumType,
                type
            }
        })
    },
    /**
    * 代理发展商户详情
    * @param {*} params
    */
    agencyDetail(companyId, payPriceType, commissionType) {
        return requestAxios({
            url: `/merchant/merchant/ger_merchant_list`,
            method: 'GET',
            params: {
                companyId, payPriceType, commissionType,
            }
        })
    }
}



