import { requestAxios } from '@/utils'
import qs from 'qs'

export default {
  /**
   * 数据概览
   * @param {*} params
   */
  dataView(companyId) {
    return requestAxios({
      url: `/auth/running_account_new/app_data_view`,
      method: 'GET',
      params: { companyId: companyId }
    })
  },
  /**
   * 交易数据
   * @param {*} params
   */
  dataTransaction(companyId,startTime,endTime) {
    return requestAxios({
      url: `/auth/running_account_new/date_transaction`,
      method: 'GET',
      params: {
        companyId,
        startTime,
        endTime
      }
    })
  },
   /**
   * 数据趋势图
   * @param {*} params
   */
  // 销售总额
  dataMap(companyId,startTime,endTime,type) {
    return requestAxios({
      url: `/auth/running_account/order_line_chart_all`,
      method: 'GET',
      params: {
        companyId,
        startTime,
        endTime,
        type
      }
    })
  },
   /**
   * 数据趋势图
   * @param {*} params
   */
  // 我的佣金
  dataMap2(companyId,startTime,endTime,type) {
    return requestAxios({
      url: `/auth/running_account/commissionAmount_line_chart_all`,
      method: 'GET',
      params: {
        companyId,
        startTime,
        endTime,
        type
      }
    })
  },
    /**
   * 支付通道占比
   * @param {*} params
   */
  payProportion(companyId,startTime,endTime,type) {
    return requestAxios({
      url: `/auth/running_account_new/data_statistics_right`,
      method: 'GET',
      params: {
        companyId,
        startTime,
        endTime,
        type
      }
    })
  },
     /**
   * 排行榜
   * @param {*} params
   */
  ranking(companyId,startTime,endTime,payPriceType) {
    return requestAxios({
      url: `/auth/running_account_new/merchant_data`,
      method: 'GET',
      params: {
        companyId,
        startTime,
        endTime,
        payPriceType
      }
    })
  },
     /**
   * 佣金列表
   * @param {*} params
   */
  CommissionRecord(companyId,startTime,endTime,type ) {
    return requestAxios({
      url: `/auth/running_account/get_day_count_commission_amount`,
      method: 'GET',
      params: {
        companyId,
        startTime,
        endTime,
        type
      }
    })
  },
     /**
   * 商户佣金数据
   * @param {*} params
   */
  CommissionAmount(merchantId,companyId,startTime,endTime,type ) {
    return requestAxios({
      url: `/auth/running_account/get_day_merchant_commission_amount`,
      method: 'GET',
      params: {
        merchantId,
        companyId,
        startTime,
        endTime,
        type
      }
    })
  },
    /**
  * 不渲染
  * @param {*} params
  */
 count_data(companyId, startTime, endTime,type) {
  return requestAxios({
      url: `/auth/running_account/count_data`,
      method: 'GET',
      params: {
          companyId, startTime, endTime,type,
      }
  })
},

}



