import * as types from '../types'

export default [{
  path: '/home',
  name: types.HOME,
  component: resolve => require(['@/pages/home/index'], resolve),
  meta: {
    isShowFooter: true,
    title: '首页'
  }
}, {
  path: '/commissionRecord',
  name: types.COMMISSIONRECORD,
  component: resolve => require(['@/pages/commissionrecord/index'], resolve),
  meta: {
    isShowFooter: false,
    title: '佣金记录'
  }
}, {
  path: '/tradeData',
  name: types.TRADEDATA,
  component: resolve => require(['@/pages/tradeData/index'], resolve),
  meta: {
    isShowFooter: false,
    title: '交易数据'
  }
},
{
  path: '/saleroom',
  name: types.SALEROOM,
  component: resolve => require(['@/pages/saleroom/index'], resolve),
  meta: {
    isShowFooter: false,
    title: '商家销售额'
  }
},
{
  path: '/myMerchant',
  name: types.MYMERCHANT,
  component: resolve => require(['@/pages/myMerchant/index'], resolve),
  meta: {
    isShowFooter: false,
    title: '我的商户'
  }
},
{
  path: '/myDetail',
  name: types.MYDETAIL,
  component: resolve => require(['@/pages/myDetail/index'], resolve),
  meta: {
    isShowFooter: false,
    title: '商户佣金数据'
  }
},
{
  path: '/agencyDetail',
  name: types.AGENCYDETAIL,
  component: resolve => require(['@/pages/agencyDetail/index'], resolve),
  meta: {
    isShowFooter: false,
    title: '代理发展的商户'
  }
},
]



