import { requestAxios } from '@/utils'
import qs from 'qs'
// import qs from 'qs'

export default {
  /**
   * 身份证ocr
   */
  cardOcr(params) {
    return requestAxios({
      url: '/ali_pay_info/ali_ocr/id/card',
      method: 'post',
      data: qs.stringify(
        params
      )
    })
  },
  /**
   * 营业执照ocr
   */
  licenseOcr(params) {
    return requestAxios({
      url: '/ali_pay_info/ali_ocr/business/license',
      method: 'post',
      data: qs.stringify(
        params
      )
    })
  },
  /**
   * 银行卡ocr
   */
  bankOcr(params) {
    return requestAxios({
      url: '/ali_pay_info/ali_ocr/bank/card',
      method: 'post',
      data: qs.stringify(
        params
      )
    })
  },
  /**
   * 校验微信进件资料
   * @param {*} id
   */
  wxCheck(id) {
    return requestAxios({
      url: `/dist/app/merchant/wx/check?id=${id}`,
      method: 'get'
    })
  },
  /**
   * 校验支付宝进件资料
   * @param {*} id
   */
  zfbCheck(id) {
    return requestAxios({
      url: `/dist/app/merchant/zfb/check?id=${id}`,
      method: 'get'
    })
  },
  /**
   * 校验支付宝进件资料
   * @param {*} id
   */
  tqsxfCheck(id) {
    return requestAxios({
      url: `/dist/app/merchant/tqsxf/check?id=${id}`,
      method: 'get'
    })
  },
  
   /**
   * 提交微信进件资料
   */
  wxInto(params) {
    return requestAxios({
      url: '/dist/app/merchant/wx/into',
      method: 'post',
      data: qs.stringify(
        params
      )
    })
  },
   /**
   * 提交支付宝进件资料
   */
  zfbInto(params) {
    return requestAxios({
      url: '/dist/app/merchant/zfb/into',
      method: 'post',
      data: qs.stringify(
        params
      )
    })
  },
  /**
   * 提交天阙进件资料
   */
  tqsxfInto(params) {
    return requestAxios({
      url: '/dist/app/merchant/tqsxf/into',
      method: 'post',
      data: qs.stringify(
        params
      )
    })
  },
  /**
   * 获取进件商户列表
   * @param {*} pageNumber
   * @param {*} pageSize
   * @param {*} status
   */
  getMerchantList(pageNumber, pageSize, status) {
    return requestAxios({
      url: '/merchant/mch_info/find_by_fuwushang',
      method: 'get',
      params: {
        pageNumber,
        pageSize,
        status
      }
    })
  },
  /**
   * 保存基本进件资料
   */
  merchantAdd(params) {
    return requestAxios({
      url: '/dist/app/merchant/add',
      method: 'post',
      data: qs.stringify(
        params
      )
    })
  },
  /**
   * 保存支付宝提交资料
   */
  zfBAdd(params) {
    return requestAxios({
      url: 'dist/app/merchant/add/special',
      method: 'post',
      data: qs.stringify(
        params
      )
    })
  },
  
  /**
   * 获取进件商户详情
   * @param {*} id
   */
  getMerchantDetail(id) {
    return requestAxios({
      url: `/dist/app/merchant/query_mchinfo?id=${id}`,
      method: 'get'
    })
  },
  /**
   * 微信进件详情
   * @param {*} id
   */
  getWxDetail(id) {
    return requestAxios({
      url: `/dist/app/merchant/wx/detail?id=${id}`,
      method: 'get'
    })
  },
  /**
   * 支付宝进件详情
   * @param {*} id
   */
  getZfbDetail(id) {
    return requestAxios({
      url: `/dist/app/merchant/zfb/detail?id=${id}`,
      method: 'get'
    })
  },
  /**
   * 天阙进件详情
   * @param {*} id
   */
  getTqsxfDetail(id) {
    return requestAxios({
      url: `/dist/app/merchant/tqsxf/detail?id=${id}`,
      method: 'get'
    })
  },
  /**
   * 获取资源图片
   * @param {*} id
   */
  getPic(id) {
    return requestAxios({
      url: `/fms/upload/resource/thumbnail/${id}`,
      method: 'get'
    })
  },

  /**
   * 上传图片
   * @param {*} file
   */
  uploadImage(file) {
    let formData = new FormData()
    formData.append('module', 'image')
    formData.append('files', file)
    return requestAxios({
      url: `/fms/upload/files_upload/file/user`,
      method: 'post',
      data: formData,
      header: {
        'Content-Type': 'multipart/form-data'
      }
    })
  },

  /**
   * 提交
   * @param {*} params
   */
  submit(params) {
    let formData = new FormData()
    for (let i in params) {
      formData.append(i, params[i])
    }
    return requestAxios({
      url: `/merchant/mch_info/update`,
      method: 'post',
      data: formData
    })
  }
}





