import * as types from '../types'

export default [{
  path: '/client_info',
  name: types.CLIENT_INFO,
  component: resolve => require(['@/pages/clientInfo/index'], resolve),
  meta: {
    isShowFooter: true,
    title: '进件'
  }
}, {
  path: '/client_info_detail',
  name: types.CLIENT_INFO_DETAIL,
  component: resolve => require(['@/pages/clientInfo/clientInfoDetail'], resolve),
  meta: {
    isShowFooter: false,
    title: '进件'
  }
},{
  path: '/client_info_add',
  name: types.CLIENT_INFO_ADD,
  component: resolve => require(['@/pages/clientInfo/clientInfoAdd'], resolve),
  meta: {
    isShowFooter: false,
    title: '进件'
  }
}, {
  path: '/client_info_record',
  name: types.CLIENT_INFO_RECORD,
  component: resolve => require(['@/pages/clientInfo/clientInfoRecord'], resolve),
  meta: {
    isShowFooter: false,
    title: '进件'
  }
}, {
  path: '/client_info_record_next',
  name: types.CLIENT_INFO_RECORD_NEXT,
  component: resolve => require(['@/pages/clientInfo/clientInfoRecordNext'], resolve),
  meta: {
    isShowFooter: false,
    title: '进件'
  }
}, {
  path: '/client_info_record_last',
  name: types.CLIENT_INFO_RECORD_LAST,
  component: resolve => require(['@/pages/clientInfo/clientInfoRecordLast'], resolve),
  meta: {
    isShowFooter: false,
    title: '进件'
  }
}, {
  path: '/submit_wx',
  name: types.SUBIT_WX,
  component: resolve => require(['@/pages/clientInfo/submitWx'], resolve),
  meta: {
    isShowFooter: false,
    title: '进件'
  }
}, {
  path: '/submit_zfb',
  name: types.SUBIT_ZFB,
  component: resolve => require(['@/pages/clientInfo/submitZfb'], resolve),
  meta: {
    isShowFooter: false,
    title: '进件'
  }
}, {
  path: '/submit_tq',
  name: types.SUBIT_TQ,
  component: resolve => require(['@/pages/clientInfo/submitTq'], resolve),
  meta: {
    isShowFooter: false,
    title: '进件'
  }
}]



